import React , {useState} from 'react'
import './Visiter.css'
import { Link } from 'react-router-dom'
import back from '../../Assets/images/back.png';
import image from '../../Assets/images/Mask Group -1.png'
import {BsPersonPlus} from "react-icons/bs"
import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from "react-hook-form";
import toast, { Toaster } from "react-hot-toast";


const schema = yup
  .object({
    email: yup.string().email().required("Please Enter your Email"),
  })


const Visiter = () => {

    const [ data , setData ] = useState({
        email:"",
        mobile_number:"",
        whatsapp_number:""
    })

    const { register, handleSubmit , formState:{errors}} = useForm({resolver: yupResolver(schema)});


    const onSubmit = ()=> {
        localStorage.setItem("users", JSON.stringify({ ...data }));
        toast.success("Now you are ready to send your message");
    }
    return (
    <div className='visiter'>
        <Link to="/" className='back'>
            <span>Back</span>
            <img src={back} alt=""/>
        </Link>
        <div className='section1'>
            <header>
                <span className='icon'><BsPersonPlus/></span>
                <span className='text'>Visitor</span>
            </header>
            <div><Toaster/></div>
            <form onSubmit = {handleSubmit(onSubmit)} encType="multipart/form-data">
            <div className='body'>
                <div>
                <input {...register("email")} value={data.email} onChange={(e)=> setData({...data , email:e.target.value})} className='my-2' type="email" placeholder='E-mail address'/>
                <span style={{color:"red"}} className="error">{errors.email?.message}</span>
                </div>
                <span>or</span>
                <div>
                    <input value={data.mobile_number} onChange={(e)=> setData({...data , mobile_number:e.target.value})} className='my-2' type="number" placeholder='mobile number'/>
                </div>
                <span>or</span>
                <div>
                    <input  value={data.whatsapp_number} onChange={(e)=> setData({...data , whatsapp_number:e.target.value})} className='my-2' type="number" placeholder='Whatsapp number'/>
                </div>
            </div>
            <div className='footer'>
                <button type="submit" className='mt-3 btn btn-dark'>Register</button>
            </div>
            </form>
        </div>
        <div className='section2'>
            <img src={image} alt=""/>
        </div>
    </div>
  )
}

export default Visiter;
